// BASIC
import React, { Component } from 'react'
import styled, {keyframes, css} from 'styled-components'
import {db} from '../../../Config/firebase'
// FUNCTIONS
import getWord from '../functions/chooseWord'
// IMAGES
import good from '../../Images/good.png'
import pl from '../../Images/icons/pl.svg'
import uk from '../../Images/icons/uk.svg'
// ICONS
import {FaHeart, FaTimes, FaSyncAlt} from 'react-icons/fa'

const EasyMatchSite = styled.div`
	display: flex;
	flex-direction: column;
	align-items: center;
	padding: 30px;
	font-size: 20px;
`
const Stats = styled.div`
	width: 100%;
	display: flex;
	justify-content: space-between;
`
const StatsElement = styled.div`
`
const StatsNumber = styled.strong`
	color: var(--color-main);
`
const like = keyframes`
	from {
		transform: translate(-50%, -50%);
		opacity: 1;
	}
	to {
		transform: translate(0%, -50%) rotate(5deg);
		opacity: 0;
	}
`
const nope = keyframes`
	from {
		transform: translate(-50%, -50%);
		opacity: 1;
	}
	to {
		transform: translate(-100%, -50%) rotate(-5deg);
		opacity: 0;
	}
`
const base = keyframes`
	from {
		opacity: 0;
		transform: translate(-50%, -50%) scale(0.9);
	}
	to {
		opacity: 1;
		transform: translate(-50%, -50%) scale(1);
	}
`
const Card = styled.div`
	width: 90vw;
	max-width: 600px;
	height: calc(100vh - 200px);
	max-height: 700px;
	position: absolute;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	padding: 20px;
	background-color: var(--color-dark);
	border-radius: 30px;
	animation: ${base} 0.2s both;
	:hover {
		cursor: pointer;
	}
	${props =>
		props.like &&
		css`
			animation: ${like} 0.2s both;
		`
	}
	${props =>
		props.nope &&
		css`
			animation: ${nope} 0.2s both;
		`
	}
`
const EndImage = styled.img`
	width: 70%;
	margin-bottom: 50px;
`
const showTitle = keyframes`
	from {
		opacity: 0;
		transform: scale(0.9);
	}
	to {
		opacity: 1;
		transform: scale(1);
	}
`
const Title = styled.h2`
	width: 100%;
	font-size: 30px;
	min-height: 25px;
	margin-bottom: 30px;
	padding: 0 20px;
	text-align: center;
	animation: ${showTitle} 0.3s ease;
	${props =>
		props.dotted &&
		css`
			font-size: 25px;
			white-space: nowrap;
			overflow: hidden;
			text-overflow: ellipsis;
		`
	};
	${props =>
		props.hide &&
		css`
			display: none;
		`
	};
`
const Image = styled.div`
	position: relative;
	width: 100%;
	height: 70%;
	background: url("${props => props.src}") no-repeat center top;
	background-size: cover;
	border-radius: 20px;
	margin-bottom: 50px;
`
const fadeAnswer = keyframes`
	from {
		opacity: 0;
	}
	to {
		opacity: 0.5;
	}
`
const answerAnimation = keyframes`
	from {
		opacity: 0;
		transform: scale(0.8);
	}
	50% {
		opacity: 1;
		transform: scale(1.1);
	}
	to {
		opacity: 1;
		transform: scale(1);
	}
`
const Answer = styled.div`
	width: 100%;
	height: 100%;
	display: none;
	justify-content: center;
	align-items: center;
	text-align: center;
	position: absolute;
	top: 0;
	left: 0;
	padding: 0 30px;
	background-color: var(--color-dark);
	opacity: 0.5;
	font-size: 30px;
	font-weight: bold;
	color: var(--color-light);
	border-radius: 20px;
	@media (min-width: 700px) and (min-height: 700px) {
		font-size: 50px;
	}
	animation: ${fadeAnswer} 0.2s both;
	::after {
		content: "${props => props.content}";
		display: -webkit-box;
		-webkit-line-clamp: 3;
		-webkit-box-orient: vertical;
		overflow: hidden;
		animation: ${answerAnimation} 0.3s both;
	}
	${props =>
		props.show &&
		css`
			display: flex;
		`
	};
`
const Decision = styled.div`
	position: absolute;
	bottom: 30px;
	display: flex;
`
const Icon = styled.div`
	font-size: 35px;
	color: var(--color-main);
	margin: 0 10px;
	transition: all 0.1s ease;
	transform: translateY(5px);
	:hover {
		cursor: pointer;
	}
	:active {
		transform: scale(1.1);
	}
`
const Language = styled.img`
	width: 45px;
	margin: 0 10px;
	transition: all 0.1s ease;
	:hover {
		cursor: pointer;
	}
	:active {
		transform: scale(0.9);
	}
`

class TinderApp extends Component {
	state = {
		languageOrder: {
			word: 'word',
			translation: 'translation'
		},
		words: [],
		baseWord: {},
		goodAnswers: [],
		badAnswers: [],
		uncertainAnswers: [],
		lastAnswer: true,
		showAnswer: false,
		stop: false,
		animation: ''
	}
	componentDidMount() {
		// db.collection('books').doc('repetytorium').onSnapshot((snap) => {
		// 	const words = Object.values(snap.data().unit_01.parts.part_01.words);
		// 	console.log(words);
		// 	const baseWord = getWord(words)
		// 	this.setState({baseWord, words});
		// })
		this.newGame();
		document.addEventListener('keydown', (e) => {
			e = e || window.event
			if (this.state.baseWord) {
				if (e.keyCode === 39) {
					this.like()
				}
				else if (e.keyCode === 37) {
					this.nope()
				}
			}
		})
	}
	newGame = () => {
		let {bookName, unitNumber} = this.props.match.params;
		if (bookName === 'repetytorium') {
			bookName = 'book_01'
		}
		else if (bookName === 'wsip') {
			bookName = 'book_02'
		}
		else if (bookName === 'jezyk-angielski-zawodowy') {
			bookName = 'book_03'
		}
		else if (bookName === 'znaki-drogowe') {
			bookName = 'book_04'
		}
		else if (bookName === 'czasowniki-nieregularne') {
			bookName = 'book_05'
		}
		let partNumber;
		if (unitNumber.includes('.') && Number(unitNumber)) {
			partNumber = unitNumber.replace(/[0-9]*\./g,'');
			unitNumber = unitNumber.replace(/\.[0-9]*/g, '').padStart(2, '0');
		}
		else if (Number(unitNumber)) {
			unitNumber = unitNumber.padStart(2, '0');
		}
		else {
			alert('nie ma strony!');
		}
		const unit = `unit_${unitNumber}`;
		const part = partNumber ? `part_${partNumber.padStart(2, '0')}` : 'test';
		db.collection('books').get().then((snaps) => {
			let units = [];
			let result = {};
			snaps.forEach(snap => {
				if (snap.data().info.id.includes(this.props.match.params.bookName)) {
					units = units.concat(Object.values(snap.data()).slice(1))
				}
			})
			units.forEach((prop, index) => result[`unit_${(index + 1).toString().padStart(2, '0')}`] = prop);
			let partWords = [];
			const words = result[unit];
			if (part !== 'test') {
				partWords = Object.values(words.parts[part].words);
			}
			else {
				Object.values(words.parts).forEach(({words}) => {
					words = words ? Object.values(words) : [];
					partWords = partWords.concat(Object.values(words));
				});
			}
			this.setState({
				goodAnswers: [],
				words: partWords,
				baseWord: getWord(partWords),
				info: {
					unit,
					part,
					bookName
				},
			});
		})
	}
	changeLanguage = () => {
		if (this.state.languageOrder.word === 'word') {
			this.setState({
				languageOrder: {
					word: 'translation',
					translation: 'word'
				},
			})
		}
		else {
			this.setState({
				languageOrder: {
					word: 'word',
					translation: 'translation'
				},
			})
		}
	}
	getAnswer = () => {
		if (!this.state.stop) {
			if (!this.state.showAnswer) {
				this.setState({showAnswer: true})
				setTimeout(() => {
					this.setState({showAnswer: false})
				}, 800);
			}
			else {
				this.setState({showAnswer: false})
			}
		}
	}
	getNew = () => {
		this.setState({
			animation: '',
			showAnswer: false
		})
		if (this.state.badAnswers.length > 0 && this.state.lastAnswer) {
			this.setState({
				baseWord: this.state.badAnswers[this.state.badAnswers.length - 1]
			})
		}
		else {
			this.setState({
				baseWord: getWord(this.state.words)
			})
		}
	}
	like = () => {
		if (!this.state.animation && !this.state.showAnswer) {
			this.setState({animation: 'like', lastAnswer: true})
			if (!this.state.goodAnswers.includes(this.state.baseWord)) {
				this.state.goodAnswers.push(this.state.baseWord)
			}
			const goodWords = [];
			this.state.goodAnswers.forEach(answer => goodWords.push(answer.word1))
			this.setState({
				words: this.state.words.filter(({word1}) => !goodWords.includes(word1))
			})
			setTimeout(() => {
				this.getNew()
				if (this.state.badAnswers.length > 0) {
					this.state.uncertainAnswers.push(this.state.badAnswers.pop())
				}
			}, 200)
		}
	}
	nope = () => {
		const swipeNope = () => {
			this.setState({animation: 'nope', lastAnswer: false})
				const badWords = [];
				this.state.badAnswers.forEach(answer => badWords.push(answer.word1))
				this.setState({
					words: this.state.words.filter(({word1}) => !badWords.includes(word1))
				})
				this.state.badAnswers.push(this.state.baseWord)
				setTimeout(() => {
					this.getNew()
				}, 200)
		}
		if (!this.state.animation && !this.state.stop) {
			if (!this.state.showAnswer) {
				this.setState({showAnswer: true, stop: true})
				setTimeout(() => {
					setTimeout(() => {
						this.state.showAnswer && this.setState({showAnswer: false, stop: false})
					}, 200)
					swipeNope()
				}, 600)
			}
			else {
				swipeNope()
			}
		}
	}
	render() {
		const {image} = this.state.baseWord ? this.state.baseWord : {};
		const word = this.state.baseWord ? this.state.baseWord[`${this.state.languageOrder.word}1`] : ''
		const translation = this.state.baseWord ? this.state.baseWord[`${this.state.languageOrder.translation}1`] : ''
		return (
			<EasyMatchSite>
				<Stats>
					<StatsElement>Umiem: <StatsNumber>{this.state.goodAnswers.length}</StatsNumber></StatsElement>
					<StatsElement>Nie umiem: <StatsNumber>{this.state.badAnswers.length}</StatsNumber></StatsElement>
				</Stats>
					{!this.state.baseWord ? (
						<Card>
							<Title>Koniec gry! Świetna robota!</Title>
							<EndImage src={good} />
							<Icon onClick={this.newGame}>
								<FaSyncAlt />
							</Icon>
						</Card>
					) : (
						<Card onClick={this.getAnswer} answer={this.state.answer} like={this.state.animation === 'like'} nope={this.state.animation === 'nope'}>
							<Title dotted={image !== 'url'}>{word}</Title>
							{image !== 'url' ? <Image src={image} alt={word} answer={translation}>
								<Answer content={translation} show={this.state.showAnswer} />
							</Image> : <Title hide={!this.state.showAnswer}>{translation}</Title>}
						</Card>
					)}
					{this.state.baseWord && <Decision>
						<Icon onClick={this.nope}>
							<FaTimes />
						</Icon>
						<Language src={this.state.languageOrder.word === 'word' ? pl : uk} title={`Zmień na język ${this.state.languageOrder.word === 'word' ? 'polski' : 'angielski'}`} onClick={this.changeLanguage} />
						<Icon onClick={this.like}>
							<FaHeart />
						</Icon>
					</Decision>}
			</EasyMatchSite>
		);
	}
}

export default TinderApp